<script>
  import { onMount, onDestroy } from 'svelte';
  import * as d3 from 'd3';
  import { mapData, currentImage } from '../stores/appStore.js';

  let mapContainer;
  let svg;
  let projection;
  let path;
  let zoom;
  let mounted = false;
  let selectedPoint = null;

  $: points = $mapData.points;
  $: center = $mapData.center;

  onMount(() => {
    mounted = true;
    initializeMap();
    
    // Actualizar mapa cuando cambie la imagen del slider
    const unsubscribe = currentImage.subscribe(() => {
      updateMapHighlight();
    });

    return unsubscribe;
  });

  onDestroy(() => {
    if (svg) {
      svg.selectAll('*').remove();
    }
  });

  function initializeMap() {
    if (!mapContainer) return;

    const width = mapContainer.clientWidth;
    const height = 500;

    // Limpiar SVG anterior
    d3.select(mapContainer).selectAll('*').remove();

    // Crear SVG
    svg = d3.select(mapContainer)
      .append('svg')
      .attr('width', width)
      .attr('height', height)
      .style('background', 'linear-gradient(135deg, #e8f4fd 0%, #c3dafe 100%)');

    // Configurar proyección para Río de Janeiro
    projection = d3.geoMercator()
      .center(center)
      .scale(60000)
      .translate([width / 2, height / 2]);

    path = d3.geoPath().projection(projection);

    // Configurar zoom
    zoom = d3.zoom()
      .scaleExtent([0.5, 8])
      .on('zoom', handleZoom);

    svg.call(zoom);

    // Crear grupos para diferentes elementos
    const mapGroup = svg.append('g').attr('class', 'map-group');
    const gridGroup = mapGroup.append('g').attr('class', 'grid-group');
    const pointsGroup = mapGroup.append('g').attr('class', 'points-group');
    const labelsGroup = mapGroup.append('g').attr('class', 'labels-group');

    // Agregar grid de fondo
    createGrid(gridGroup, width, height);
    
    // Agregar elementos geográficos básicos
    createGeographicalFeatures(mapGroup, width, height);
    
    // Agregar puntos de datos
    createDataPoints(pointsGroup);
    
    // Agregar etiquetas
    createLabels(labelsGroup);

    // Agregar controles
    createMapControls();
  }

  function createGrid(gridGroup, width, height) {
    const gridSize = 30;
    
    // Líneas verticales
    for (let x = 0; x <= width; x += gridSize) {
      gridGroup.append('line')
        .attr('x1', x)
        .attr('y1', 0)
        .attr('x2', x)
        .attr('y2', height)
        .attr('stroke', 'rgba(255,255,255,0.3)')
        .attr('stroke-width', 0.5);
    }
    
    // Líneas horizontales
    for (let y = 0; y <= height; y += gridSize) {
      gridGroup.append('line')
        .attr('x1', 0)
        .attr('y1', y)
        .attr('x2', width)
        .attr('y2', y)
        .attr('stroke', 'rgba(255,255,255,0.3)')
        .attr('stroke-width', 0.5);
    }
  }

  function createGeographicalFeatures(mapGroup, width, height) {
    // Simular costa y características geográficas básicas
    const coastline = [
      [0, height * 0.7],
      [width * 0.2, height * 0.8],
      [width * 0.4, height * 0.75],
      [width * 0.6, height * 0.8],
      [width * 0.8, height * 0.85],
      [width, height * 0.9]
    ];

    const line = d3.line()
      .x(d => d[0])
      .y(d => d[1])
      .curve(d3.curveCardinal);

    // Costa
    mapGroup.append('path')
      .datum(coastline)
      .attr('d', line)
      .attr('stroke', '#4682b4')
      .attr('stroke-width', 3)
      .attr('fill', 'none')
      .attr('opacity', 0.6);

    // Área oceánica
    mapGroup.append('polygon')
      .attr('points', `${coastline.map(d => d.join(',')).join(' ')},${width},${height},0,${height}`)
      .attr('fill', 'rgba(70, 130, 180, 0.2)');

    // Montañas (representación estilizada)
    const mountains = [
      {x: width * 0.3, y: height * 0.3, height: 15},
      {x: width * 0.4, y: height * 0.25, height: 20},
      {x: width * 0.5, y: height * 0.35, height: 12},
      {x: width * 0.6, y: height * 0.2, height: 18}
    ];

    mountains.forEach(mountain => {
      mapGroup.append('polygon')
        .attr('points', `${mountain.x},${mountain.y} ${mountain.x - 10},${mountain.y + mountain.height} ${mountain.x + 10},${mountain.y + mountain.height}`)
        .attr('fill', 'rgba(139, 69, 19, 0.6)')
        .attr('stroke', 'rgba(139, 69, 19, 0.8)')
        .attr('stroke-width', 1);
    });
  }

  function createDataPoints(pointsGroup) {
    const tooltip = d3.select('body').append('div')
      .attr('class', 'map-tooltip')
      .style('opacity', 0)
      .style('position', 'absolute')
      .style('background', 'rgba(0,0,0,0.8)')
      .style('color', 'white')
      .style('padding', '10px')
      .style('border-radius', '5px')
      .style('pointer-events', 'none')
      .style('font-size', '12px')
      .style('z-index', '1000');

    pointsGroup.selectAll('.data-point')
      .data(points)
      .enter()
      .append('circle')
      .attr('class', 'data-point')
      .attr('cx', d => projection(d.coords)[0])
      .attr('cy', d => projection(d.coords)[1])
      .attr('r', 8)
      .attr('fill', (d, i) => {
        const colors = ['#ff6b6b', '#4ecdc4', '#45b7d1', '#96ceb4', '#ffeaa7', '#dda0dd', '#98d8c8', '#f7dc6f'];
        return colors[i % colors.length];
      })
      .attr('stroke', '#fff')
      .attr('stroke-width', 2)
      .style('cursor', 'pointer')
      .style('filter', 'drop-shadow(0 2px 4px rgba(0,0,0,0.3))')
      .on('mouseover', function(event, d) {
        d3.select(this)
          .transition()
          .duration(200)
          .attr('r', 12)
          .style('filter', 'drop-shadow(0 4px 8px rgba(0,0,0,0.4))');
          
        tooltip.transition()
          .duration(200)
          .style('opacity', 1);
          
        tooltip.html(`
          <strong>${d.name}</strong><br/>
          Coordenadas: ${d.coords[1].toFixed(4)}, ${d.coords[0].toFixed(4)}<br/>
          ID: ${d.id}
        `)
          .style('left', (event.pageX + 10) + 'px')
          .style('top', (event.pageY - 10) + 'px');
      })
      .on('mouseout', function() {
        d3.select(this)
          .transition()
          .duration(200)
          .attr('r', 8)
          .style('filter', 'drop-shadow(0 2px 4px rgba(0,0,0,0.3))');
          
        tooltip.transition()
          .duration(200)
          .style('opacity', 0);
      })
      .on('click', function(event, d) {
        selectPoint(d);
      });

    // Animación de entrada
    pointsGroup.selectAll('.data-point')
      .style('opacity', 0)
      .transition()
      .delay((d, i) => i * 100)
      .duration(500)
      .style('opacity', 1)
      .attr('r', 8);
  }

  function createLabels(labelsGroup) {
    labelsGroup.selectAll('.point-label')
      .data(points)
      .enter()
      .append('text')
      .attr('class', 'point-label')
      .attr('x', d => projection(d.coords)[0])
      .attr('y', d => projection(d.coords)[1] - 15)
      .attr('text-anchor', 'middle')
      .attr('font-size', '11px')
      .attr('font-weight', 'bold')
      .attr('fill', '#333')
      .attr('stroke', '#fff')
      .attr('stroke-width', 2)
      .attr('paint-order', 'stroke')
      .text(d => d.name)
      .style('pointer-events', 'none')
      .style('opacity', 0)
      .transition()
      .delay((d, i) => i * 100 + 300)
      .duration(500)
      .style('opacity', 1);
  }

  function createMapControls() {
    const controlsDiv = d3.select(mapContainer.parentNode)
      .append('div')
      .attr('class', 'map-controls')
      .style('position', 'absolute')
      .style('top', '10px')
      .style('right', '10px')
      .style('z-index', '100');

    // Botón de reset
    controlsDiv.append('button')
      .attr('class', 'map-control-button')
      .style('background', '#667eea')
      .style('color', 'white')
      .style('border', 'none')
      .style('padding', '8px 12px')
      .style('margin', '2px')
      .style('border-radius', '4px')
      .style('cursor', 'pointer')
      .text('🏠 Reset')
      .on('click', resetZoom);

    // Botones de zoom
    controlsDiv.append('button')
      .attr('class', 'map-control-button')
      .style('background', '#667eea')
      .style('color', 'white')
      .style('border', 'none')
      .style('padding', '8px 12px')
      .style('margin', '2px')
      .style('border-radius', '4px')
      .style('cursor', 'pointer')
      .text('➕')
      .on('click', () => zoomIn());

    controlsDiv.append('button')
      .attr('class', 'map-control-button')
      .style('background', '#667eea')
      .style('color', 'white')
      .style('border', 'none')
      .style('padding', '8px 12px')
      .style('margin', '2px')
      .style('border-radius', '4px')
      .style('cursor', 'pointer')
      .text('➖')
      .on('click', () => zoomOut());
  }

  function handleZoom(event) {
    const { transform } = event;
    svg.select('.map-group').attr('transform', transform);
  }

  function resetZoom() {
    svg.transition()
      .duration(750)
      .call(zoom.transform, d3.zoomIdentity);
  }

  function zoomIn() {
    svg.transition()
      .duration(300)
      .call(zoom.scaleBy, 1.5);
  }

  function zoomOut() {
    svg.transition()
      .duration(300)
      .call(zoom.scaleBy, 1 / 1.5);
  }

  function selectPoint(point) {
    selectedPoint = point;
    
    // Highlight del punto seleccionado
    svg.selectAll('.data-point')
      .transition()
      .duration(300)
      .attr('r', d => d === point ? 15 : 8)
      .style('opacity', d => d === point ? 1 : 0.6);

    // Conectar con la imagen actual si es posible
    if (point.id !== undefined) {
      currentImage.set(point.id);
    }
  }

  function updateMapHighlight() {
    if (!svg) return;
    
    // Highlight basado en la imagen actual
    svg.selectAll('.data-point')
      .transition()
      .duration(300)
      .attr('r', (d, i) => i === $currentImage % points.length ? 12 : 8)
      .style('opacity', (d, i) => i === $currentImage % points.length ? 1 : 0.7);
  }

  function handleResize() {
    if (mounted && mapContainer) {
      initializeMap();
    }
  }
</script>

<svelte:window on:resize={handleResize} />

<div class="map-section" class:mounted>
  <h2>🗺️ Mapa de Río de Janeiro</h2>
  <p class="description">
    Visualización geoespacial interactiva de los puntos de datos en la región metropolitana.
    Haga clic en los puntos para explorar o use zoom y pan para navegar.
  </p>

  <div class="map-container" bind:this={mapContainer}>
    <!-- El mapa se renderiza aquí con D3 -->
  </div>

  {#if selectedPoint}
    <div class="point-info">
      <h3>📍 Punto Seleccionado</h3>
      <div class="point-details">
        <div class="detail-item">
          <strong>Nombre:</strong> {selectedPoint.name}
        </div>
        <div class="detail-item">
          <strong>Coordenadas:</strong> {selectedPoint.coords[1].toFixed(4)}, {selectedPoint.coords[0].toFixed(4)}
        </div>
        <div class="detail-item">
          <strong>ID:</strong> {selectedPoint.id}
        </div>
        <button 
          class="view-image-btn" 
          on:click={() => currentImage.set(selectedPoint.id)}
        >
          🖼️ Ver Imagen Asociada
        </button>
      </div>
    </div>
  {/if}

  <div class="map-stats">
    <div class="stats-grid">
      <div class="stat-card">
        <div class="stat-icon">📊</div>
        <div class="stat-content">
          <div class="stat-number">{points.length}</div>
          <div class="stat-label">Puntos de Datos</div>
        </div>
      </div>
      
      <div class="stat-card">
        <div class="stat-icon">🎯</div>
        <div class="stat-content">
          <div class="stat-number">{$currentImage}</div>
          <div class="stat-label">Imagen Actual</div>
        </div>
      </div>
      
      <div class="stat-card">
        <div class="stat-icon">🌍</div>
        <div class="stat-content">
          <div class="stat-number">RJ</div>
          <div class="stat-label">Río de Janeiro</div>
        </div>
      </div>
    </div>
  </div>

  <div class="map-legend">
    <h3>🗂️ Leyenda del Mapa</h3>
    <div class="legend-items">
      <div class="legend-item">
        <div class="legend-color" style="background: #4682b4;"></div>
        <span>Características Acuáticas</span>
      </div>
      <div class="legend-item">
        <div class="legend-color" style="background: rgba(139, 69, 19, 0.6);"></div>
        <span>Elevaciones/Montañas</span>
      </div>
      <div class="legend-item">
        <div class="legend-color" style="background: #ff6b6b;"></div>
        <span>Puntos de Datos</span>
      </div>
      <div class="legend-item">
        <div class="legend-color grid"></div>
        <span>Grid de Referencia</span>
      </div>
    </div>
    
    <div class="map-instructions">
      <h4>💡 Instrucciones:</h4>
      <ul>
        <li>🖱️ <strong>Clic:</strong> Seleccionar punto</li>
        <li>🔍 <strong>Scroll:</strong> Zoom in/out</li>
        <li>👆 <strong>Arrastrar:</strong> Pan/mover mapa</li>
        <li>🏠 <strong>Reset:</strong> Volver a vista inicial</li>
      </ul>
    </div>
  </div>
</div>

<style>
  .map-section {
    opacity: 0;
    transform: translateY(30px);
    transition: all 0.8s ease;
  }

  .map-section.mounted {
    opacity: 1;
    transform: translateY(0);
  }

  .map-section h2 {
    color: #667eea;
    margin-bottom: 20px;
    font-size: 2rem;
    background: linear-gradient(135deg, #667eea, #764ba2);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .description {
    margin-bottom: 25px;
    color: #666;
    font-size: 1.1rem;
    text-align: center;
    line-height: 1.6;
  }

  .map-container {
    position: relative;
    width: 100%;
    height: 500px;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 8px 25px rgba(0,0,0,0.15);
    background: #e8f4fd;
    margin-bottom: 25px;
    border: 2px solid rgba(102, 126, 234, 0.1);
  }

  .point-info {
    background: linear-gradient(135deg, rgba(102, 126, 234, 0.1), rgba(118, 75, 162, 0.1));
    padding: 20px;
    border-radius: 12px;
    margin-bottom: 25px;
    border: 1px solid rgba(102, 126, 234, 0.2);
    animation: slideIn 0.5s ease;
  }

  @keyframes slideIn {
    from {
      opacity: 0;
      transform: translateX(-20px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }

  .point-info h3 {
    color: #667eea;
    margin-bottom: 15px;
    font-size: 1.3rem;
  }

  .point-details {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 15px;
    align-items: center;
  }

  .detail-item {
    font-size: 0.95rem;
    color: #333;
  }

  .detail-item strong {
    color: #667eea;
  }

  .view-image-btn {
    background: linear-gradient(135deg, #667eea, #764ba2);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 25px;
    cursor: pointer;
    font-size: 0.9rem;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
  }

  .view-image-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
  }

  .map-stats {
    margin-bottom: 25px;
  }

  .stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 15px;
  }

  .stat-card {
    background: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0,0,0,0.1);
    display: flex;
    align-items: center;
    gap: 15px;
    transition: all 0.3s ease;
    border: 1px solid rgba(102, 126, 234, 0.1);
  }

  .stat-card:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 20px rgba(102, 126, 234, 0.2);
  }

  .stat-icon {
    font-size: 2rem;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, #667eea, #764ba2);
    border-radius: 50%;
    color: white;
  }

  .stat-content {
    flex: 1;
  }

  .stat-number {
    font-size: 1.8rem;
    font-weight: bold;
    color: #667eea;
    line-height: 1;
  }

  .stat-label {
    font-size: 0.85rem;
    color: #666;
    margin-top: 2px;
  }

  .map-legend {
    background: rgba(118, 75, 162, 0.05);
    padding: 20px;
    border-radius: 12px;
    border: 1px solid rgba(118, 75, 162, 0.1);
  }

  .map-legend h3 {
    color: #764ba2;
    margin-bottom: 15px;
    font-size: 1.2rem;
  }

  .legend-items {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 12px;
    margin-bottom: 20px;
  }

  .legend-item {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 0.9rem;
    color: #333;
  }

  .legend-color {
    width: 20px;
    height: 20px;
    border-radius: 4px;
    border: 1px solid rgba(0,0,0,0.2);
  }

  .legend-color.grid {
    background: repeating-linear-gradient(
      45deg,
      rgba(255,255,255,0.3),
      rgba(255,255,255,0.3) 2px,
      transparent 2px,
      transparent 4px
    );
  }

  .map-instructions {
    border-top: 1px solid rgba(118, 75, 162, 0.2);
    padding-top: 15px;
  }

  .map-instructions h4 {
    color: #764ba2;
    margin-bottom: 10px;
    font-size: 1rem;
  }

  .map-instructions ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 8px;
  }

  .map-instructions li {
    font-size: 0.85rem;
    color: #555;
    padding: 4px 0;
  }

  /* Estilos globales para controles del mapa */
  :global(.map-tooltip) {
    pointer-events: none !important;
    z-index: 1000;
  }

  :global(.map-controls) {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  :global(.map-control-button:hover) {
    background: #5a67d8 !important;
    transform: scale(1.05);
  }

  /* Responsive Design */
  @media (max-width: 768px) {
    .map-container {
      height: 400px;
    }

    .point-details {
      grid-template-columns: 1fr;
    }

    .stats-grid {
      grid-template-columns: 1fr;
    }

    .legend-items {
      grid-template-columns: 1fr;
    }

    .map-instructions ul {
      grid-template-columns: 1fr;
    }

    .map-section h2 {
      font-size: 1.6rem;
    }
  }

  @media (max-width: 480px) {
    .map-container {
      height: 350px;
    }

    .stat-card {
      padding: 15px;
    }

    .stat-icon {
      width: 40px;
      height: 40px;
      font-size: 1.5rem;
    }

    .stat-number {
      font-size: 1.5rem;
    }
  }
</style>